// @flow

/**
 * index.js - возвращает объект с выбранным API
 * @module api/index
 * @requires module:config
 * @requires module:api/mockup
 * @requires module:api/linkpulse
 * @requires module:api/yandex
 * */

import {dataProviderTypes} from '../config'
import * as linkpulse from './linkpulse'
import * as yandex from './yandex'
import * as mockup from './mockup'

const dataProviders = {
    [dataProviderTypes.LINKPULSE]: linkpulse,
    [dataProviderTypes.YANDEX]: yandex,
    [dataProviderTypes.MOCKUP]: mockup,
}

export const getAPI = (type: string = dataProviderTypes.LINKPULSE, callback: () => any): any => {
    return callback.call(this, dataProviders[dataProviderTypes.MOCKUP])
}



