// @flow

/**
 * yandex.js - файл конфигурации API yandex
 * @module config/yandex
 * */


/**
 * Временный access token можно получить тут:
 * https://oauth.yandex.ru/authorize?response_type=token&client_id=<CLIENT_ID>
 * */
export const yandexConfig: {
    env: {
        prod: {
            URL: string
        },
        dev: {
            URL: string
        }
    },
} = {
    env: {
        prod: {
            URL: `${window.location.protocol}//${global.location.host}/admin/aerostat/build/yandex/api.php`
        },
        dev: {
            URL: 'http://dev6.womansday.ru/admin/aerostat/build/yandex/api.php'
        },
    }
}


