// @flow

/**
 * CurrentUsersActions.js - действия и генераторы действий для добавления данных о количестве пользователей в хранилище
 * @module actions/TopArticles
 * @requires module:config
 * @requires module:constants/TopArticles
 * @requires module:common/index
 * @requires module:api/index
 * */

import {SET_TOP_ARTICLES} from '../constants/TopArticles'
import {interval} from '../common'
import {POLLING_INTERVAL, dataProviderTypes} from '../config'
import {getAPI} from '../api'

/**
 * @memberof actions/TopArticles
 * */
export function setTopArticles(topArticles: TopArticles): TopArticlesAction {
    return {
        type: SET_TOP_ARTICLES,
        topArticles: topArticles
    }
}

/**
 * Запрашиваем просмотры страниц, заголовки и URL-ы TOP статей через API в интервале
 * @memberof actions/TopArticles
 * maxArticlesCount: number, sort: 'pageviews' | 'viewtime' = 'pageviews', providerType: string = dataProviderTypes.LINKPULSE
 * */
export function fetchTopArticles({
                                     maxArticlesCount = 0,
                                     sort = 'pageviews',
                                     dateRange,
                                     providerType = dataProviderTypes.LINKPULSE,
                                 }
                                 : {
                                     maxArticlesCount: number,
                                     sort: 'pageviews' | 'viewtime',
                                     dateRange?: [string, string],
                                     providerType: string,
                                 }) {
    return (dispatch: any) => {
        dispatch(setTopArticles([]))
        getAPI(providerType, (api) => {
            interval(() => {
                api.fetchTopArticles(maxArticlesCount, sort, dateRange).then((topArticles: TopArticles) => {
                    dispatch(setTopArticles(topArticles))
                }).catch((error) => {
                    console.log(error)
                })
            }, POLLING_INTERVAL, true, 'fetchTopArticles')
        })
    }
}



