/**
 * Возвращает Promise с массивом данных ТОП статей
 * @memberof api/mockup
 * */
export function fetchTopArticles(maxArticlesCount: number, sort: 'pageviews' | 'viewtime' = 'pageviews'): any { // К сожалению, Flow не позволяет указать диапазон доступных значений: 1<= maxArticlesCount <=20
    return new Promise((resolve) => {
        let titles: string[] = [
            'Дмитрий Нагиев подрался на автостраде',
            'Минус десять лет: Киркоров сменил имидж и помолодел - Woman\'s Day',
            'Кто такой Никита Турчин из Битвы экстрасенсов - Woman\'s Day',
            'Звезда «Дома-2» Екатерина Колесниченко показала фото после неудачной пластики - Woman\'s Day',
            'Яна Кошкина: «Моего мужчину хотят большинство женщин»',
            'Стало известно, почему Кейт Миддлтон не красит ногти',
            'Минус десять лет: Киркоров сменил имидж и помолодел - Woman\'s Day',
            'Дешево и сердито: Перминова пользуется маской за 100 рублей',
            '5 ошибок в стиле, которые вы совершаете каждый день',
            'Вылитая Кристина! Пугачева показала фото подросшей внучки',
            'Звезда «Дома-2» Екатерина Колесниченко показала фото после неудачной пластики - Woman\'s Day',
            'Как одеться, чтобы не замерзнуть: 10 лайфхаков',
            'Что будет, если долго не заниматься сексом: испытано на себе - Woman\'s Day',
            'Рост звезд Голливуда: смотреть точные данные, фото - Woman\'s Day',
            'Фитнес-модели: тренировки, фото, советы тренера - Woman\'s Day',
            'Алла Пугачева: последние новости 2017 - Woman\'s Day',
            'Паулина Андреева вышла в свет в старомодном наряде',
            'Актеры которых не узнать без грима фото - Woman\'s Day',
            'Марина и Ирина Голомаздины под маркой 12Storeez выпустили коллекцию одежды для пожилых женщин: фото, подробности - Woman\'s Day',
            'Муж Ольги Рапунцель: «Если нужен развод, я готов его дать»',
        ],
        maxViewsCount: number = 200

        const pageviewsList = new Array(maxArticlesCount)
        .fill(0)
        .map(() => Math.ceil(Math.random() * maxViewsCount))
    
        const summ = pageviewsList.reduce(function(summ, current) {
            return summ + current
        }, 0)
        
        let topArticles = new Array(maxArticlesCount)
        .fill(0)
        .map(function (element, index) {
            const pageviews = pageviewsList[index]
            const pagetime = moment(new Date(0, 0, 0, 0, 0, Math.ceil(Math.random() * maxViewsCount), 0)).format('mm:ss')
            const percent = pageviews * 100 / (summ / 5)
            let counters = null
            
            return {
                value: [pageviews, pagetime],
                percent: percent,
                url: '/' + Math.ceil(Math.random() * maxArticlesCount),
                title: titles[index] || 'Lorem ipsum dolor sit amet consectetur adipiscing elit',
                caption: 'not set',
                counters: counters
            }
        })
        .sort(function ({value: [valueA1, valueA2]}, {value: [valueB1, valueB2]}) {
            if (sort === 'pageviews') {
                if (valueA1 < valueB1) {
                    return 1
                }
                if (valueA1 > valueB1) {
                    return -1
                }
                
                return 0
            }
    
            if (valueA2 < valueB2) {
                return 1
            }
            if (valueA2 > valueB2) {
                return -1
            }
            
            return 0
        })
        .map((item, index) => {
            if (index < 5) {
                item.counters = [
                    {
                        value: Math.ceil(Math.random() * maxViewsCount),
                        id: 'vk',
                        title: 'Вконтакте',
                    },
                    {
                        value: Math.ceil(Math.random() * maxViewsCount),
                        id: 'ok',
                        title: 'Одноклассники',
                    },
                    {
                        value: Math.ceil(Math.random() * maxViewsCount),
                        id: 'facebook',
                        title: 'Facebook',
                    },
                    {
                        value: Math.ceil(Math.random() * maxViewsCount),
                        id: 'instagram',
                        title: 'Instagram',
                    },
                    {
                        value: Math.ceil(Math.random() * maxViewsCount),
                        id: 'yandex',
                        title: 'Zen и Я.Новости',
                    },
                ]
            }
            
            return item;
        })

        resolve(topArticles)
    })
}

/**
 * Возвращает Promise с количеством пользователей в течение дня и неделю назад
 * @memberof api/mockup
 * */
export function fetchUsersTimeline(range: string = 'today', url?: string): any {
    return new Promise((resolve) => {
        let data = []
        let interval: number = 0
        let from = new Date()
        let to = new Date()
    
        from.setHours(0)
        from.setMinutes(0)
        from.setSeconds(0)
        from.setMilliseconds(0)
    
        to.setHours(23)
        to.setMinutes(59)
        to.setSeconds(0)
        to.setMilliseconds(0)
        
        if (range === 'today') {
            interval = (24 * 60) / 20 / 2
        }
    
        if (range === 'weekAgo') {
            interval = (24 * 60) / 20
        }
        
        for (let i = 0; i < interval; i++) {
            let date = new Date(from)
            date.setMinutes(i * 20)
    
            let total = Math.ceil( (1 - Math.cos(i / 10 + Math.random() / 2) ) * 1000)
    
            data.push({
                date: date.toISOString(),
                total: total,
            })
        }
    
        resolve(data)
    })
}

/**
 * Возвращает Promise с массивом данных о топе источников
 * @memberof api/mockup
 * */
export function fetchTopReferers(maxReferersCount: number = 10, url?: string): any {
    return new Promise((resolve) => {
        let titles: string[] = [
            'google.ru',
            'yandex.ru',
            'google.com',
            'vk.com',
            'ok.ru',
            'mobile.vk.com',
            'mobile.ok.com',
            'google.com.ua',
            'ngs.ru',
            '74.ru',
        ],
        maxViewsCount: number = 100
        
        let list = new Array(maxReferersCount)
        .fill(0)
        .map(function (element, index) {
            const value = Math.ceil(Math.random() * maxViewsCount)
            const percent = value * 100 / maxViewsCount
            
            return element = {
                value: value,
                percent: percent,
                url: '',
                title: titles[index]
            }
        })
        .sort(function (a, b) {
            if (a.value < b.value) {
                return 1
            }
            if (a.value > b.value) {
                return -1
            }
            return 0
        })
    
        resolve(list)
    })
}

/**
 * Возвращает Promise с массивом данных о топе следующих переходов для ссылки
 * @memberof api/mockup
 * */
export function fetchUrlTopNextPages(maxItems: number = 10, url: string): any {
    return new Promise((resolve) => {
        let titles: string[] = [
            'link 1',
            'link 2',
            'link 3',
        ],
        maxViewsCount: number = 100
        
        let list = new Array(maxItems)
        .fill(0)
        .map(function (element, index) {
            const value = Math.ceil(Math.random() * maxViewsCount)
            const percent = value * 100 / maxViewsCount
            
            return {
                value: value,
                percent: percent,
                url: `testLink${index}`,
                title: titles[index]
            }
        })
        .sort(function (a, b) {
            if (a.value < b.value) {
                return 1
            }
            if (a.value > b.value) {
                return -1
            }
            return 0
        })
        
        resolve(list)
    })
}

/**
 * Возвращает Promise с данными о странице
 * @memberof api/mockup
 * */
export function fetchPageInfo(url: string): any {
    return new Promise((resolve) => {
        resolve({
            count: Math.ceil(Math.random() * 1000),
            viewtime: Math.ceil(Math.random() * 100),
            recirculation: Math.ceil(Math.random() * 100),
            title: 'Заголовок страницы',
            url: `http://${url}`,
            image: 'http://n1s2.hsmedia.ru/40/70/95/40709538f96017c506f0546b2b5ae177/1200x630_21_0a18dddc8d8a8493db051039a8ea8c42@1200x630_0xc0a839a2_17786530471507734067.jpeg',
            author: 'Наташа Белая',
            date: new Date().toISOString(),
        })
    })
}

/**
 * Возвращает Promise с данными об устройствах
 * @memberof api/mockup
 * */
export function fetchDevices(url?: string): any {
    return new Promise((resolve) => {
        const data = [
            {
                title: 'Desktop',
                value: Math.ceil(Math.random() * 1000),
                percent: 0,
                url: '',
            },
            {
                title: 'Tablet',
                value: Math.ceil(Math.random() * 1000),
                percent: 0,
                url: '',
            },
            {
                title: 'Mobile',
                value: Math.ceil(Math.random() * 1000),
                percent: 0,
                url: '',
            },
        ]
        .sort(function (a, b) {
            if (a.value < b.value) {
                return 1
            }
            if (a.value > b.value) {
                return -1
            }
            return 0
        })
    
        const summ = data.reduce(function(summ, current) {
            return summ + current.value
        }, 0)
    
        data.forEach((item) => {
            item.percent = item.value * 100 / summ
        })
        
        resolve(data)
    })
}



