// @flow

/**
 * Layout.js - общий layout для страниц
 * @module components/Layout
 * @requires module:components/Nav
 * */

import React from 'react'
import {Nav} from '../../components/Nav/Nav'

import './Layout.css'

type Props = {
    children: any
}

/**
 * @namespace
 * */
export const Layout = (props: Props) => {
    /**
     * Отрисовка компонента при изменении состояния
     * @memberof Layout
     * @alias render
     * */
    return (
    <div className="Layout">
        <div className="container-fluid">
            <div className="row">
                <div className="col-12 p-0 mb-3">
                    <Nav/>
                </div>
            </div>
        </div>
        <div className="container-fluid">
            {props.children}
        </div>
    </div>
    )
}


