// @flow

/**
 * Nav.js - компонент меню
 * @module components/Nav
 * */

import React from 'react'
import {Link} from 'react-router-dom'

import './Nav.css'

import * as sourceIconLogo from './logo.svg'

/**
 * @namespace
 * */
export const Nav = () => {
    /**
     * Отрисовка компонента при изменении состояния
     * @memberof Nav
     * @alias render
     * */
    return (
    <div className="Nav">
        <Link className="Nav__logo" to='/'>
            <div className="Nav__logoIcon">
                <img width="40" src={sourceIconLogo} alt=""/>
            </div>
            <div className="Nav__logoType">aerostat</div>
        </Link>
        <ul className="nav">
            <li className="nav-item">
                <Link className="nav-link active text-light" to='/'>Главная</Link>
            </li>
            <li className="nav-item">
                <Link className="nav-link text-light" to='/tops'>Топы</Link>
            </li>
        </ul>
    </div>
    )
}



