/**
 * index.js - функции-хелперы
 * @module common/index
 * */

/**
 * Map со всеми id от setInterval
 * @memberof common/index
 * */
export const intervals = {}

/**
 * Функция-обертка над setInterval, запускает первый интервал без задежрки
 * @memberof common/index
 * */
export function interval(callback, timer, runImmediately = true, key = '') {
    if (runImmediately) {
        callback()
    }
    key && removeInterval(key)
    const id = setInterval(() => callback(), timer)
    const hash = (key) ? key : id
    intervals[hash] = id
}

/**
 * Очищает все интервалы
 * @memberof common/index
 * */
export function clearAllIntervals() {
    Object.keys(intervals).forEach((key) => clearInterval(intervals[key]))
}

/**
 * Очищает интервал
 * @memberof common/index
 * */
export function removeInterval(key) {
    clearInterval(intervals[key])
}

/**
 * Добавляет GET параметры к объекту URL
 * @param {URL} url
 * @param {object} params
 * @memberof common/index
 * @see https://developer.mozilla.org/en-US/docs/Web/API/URL/URL
 * */
export function bindParamsToUrl(url, params) {
    Object.keys(params).forEach(key => url.searchParams.set(key, params[key]))
    return url
}

/**
 * Считает рециркуляцию
 * @memberof common/index
 * */
export function getRecirculation(count: number, clicks: number): number {
    if (count === 0 || clicks === 0) {
        return 0
    }
    
    return global.Math.ceil((clicks / count) * 100)
}

/**
 * Фильтруем массив объектов по строковым значениям
 * @memberof common/index
 * */
export function filterListByValue(list: any, filter: string) {
    if (!list.length || !filter) {
        return list
    }
    
    const findInProperty = (property) => typeof property === 'string' && property.toLocaleLowerCase().indexOf(filter.toLocaleLowerCase()) >= 0
    
    return [...list.filter((item) => Object.keys(item).filter((key) => findInProperty(item[key])).length)]
}

/**
 * Возвращает свойство объекта если оно существует
 * @memberof common/index
 * @example
 * getPropSafe(() => foo.bar.baz)
 * */
export function getPropSafe(func: () => any) {
    try {
        return func()
    } catch (error) {
        return undefined
    }
}



