// @flow

/**
 * yandex.js - yandex API
 *
 * Пример запроса для яндекса:
 * https://api-metrika.yandex.ru/stat/v1/data/?oauth_token=AQAAAAABtQJ0AAQKmsQVnnUNXUf-tRCGDwjxRxY&id=421459&preset=tech_platforms&dimensions=ym%3As%3Abrowser
 *
 * Пример API для php:
 * https://github.com/nixsolutions/yandex-php-library/blob/master/examples/OAuth/callback.php
 *
 * Отчет для топа статей:
 * https://metrika.yandex.ru/stat/5a980b5d8beed1bffa2024ed?period=today&id=421459&stateHash=5a99291dbf7bf803eada62a0
 * https://api-metrika.yandex.ru/stat/v1/data?ids=421459&oauth_token=AQAAAAABtQJ0AAQKmsQVnnUNXUf-tRCGDwjxRxY&date1=today&date2=today&metrics=ym:s:pageviews,ym:s:avgVisitDurationSeconds&dimensions=ym:s:startURLPathLevel4
 *
 * @module api/yandex
 * */

import Promise from 'es6-promise'
import fetch from 'isomorphic-fetch'
import moment from 'moment'
import {bindParamsToUrl, getPropSafe} from '../../common'
import {yandexConfig} from '../../config/yandex'

let baseUrl = ''

if (process.env.NODE_ENV === 'production' && global.location.host.indexOf('localhost') < 0) {
    baseUrl = yandexConfig.env.prod.URL
} else {
    baseUrl = yandexConfig.env.dev.URL
}

type YandexTopArticles = {
    data: {
        dimensions: [{
            name: string,
        }]
    },
    metrics: [number, number]
}

/**
 * Возвращает Promise с количеством просмотров, заголовками, URL-ами и относительными процентами просмотров TOP статей
 * @memberof api/linkpulse
 * */
export function fetchTopArticles(maxArticlesCount: number, sort: 'pageviews' | 'viewtime' = 'pageviews', dateRange: [string, string]): Promise<ListType> {
    const params = {
        date1: getPropSafe(() => dateRange[0]) || 'today',
        date2: getPropSafe(() => dateRange[1]) || 'today',
        metrics: 'ym:s:pageviews,ym:s:avgVisitDurationSeconds',
        dimensions: 'ym:s:startURLPathLevel4',
        limit: maxArticlesCount,
        sort: sort === 'viewtime'
            ? '-ym:s:avgVisitDurationSeconds'
            : '-ym:s:pageviews',
    }
    
    const url = bindParamsToUrl(new global.URL(baseUrl), params)
    
    return new Promise((resolve, reject) => {
        fetch(url, {
            credentials: 'include'
        }).then((response) => {
            return response.json()
        }).then((data: YandexTopArticles) => {
            const topArticles = data.data.map((item) => {
                const value = [getPropSafe(() => item.metrics[0]), moment(new Date(0, 0, 0, 0, 0, getPropSafe(() => item.metrics[1]) || 0, 0)).format('HH:mm:ss')]
                const title = getPropSafe(() => item.dimensions[0].name)
                const url = getPropSafe(() => item.dimensions[0].name)
                const percent = 0
                
                return {value, title, percent, url}
            })
            
            resolve(topArticles)
        }).catch((error) => {
            reject(error)
        })
    })
}



