// @flow

/**
 * linkpulse.js - linkpulse API
 * @module api/linkpulse
 * */

import Promise from 'es6-promise'
import fetch from 'isomorphic-fetch'
import moment from 'moment'
import {bindParamsToUrl, getRecirculation, getPropSafe} from '../../common'
import {linkpulseConfig} from '../../config/linkpulse'

let baseUrl = ''

if (process.env.NODE_ENV === 'production' && global.location.host.indexOf('localhost') < 0) {
    baseUrl = linkpulseConfig.env.prod.URL
} else {
    baseUrl = linkpulseConfig.env.dev.URL
}

/**
 * Возвращает Promise с количеством онлайн пользователей
 * @memberof api/linkpulse
 * */
export function fetchCurrentUsers() {
    fetchCurrentUsers.params = fetchCurrentUsers.params || {
        'query': 'uniqueusers',
        'filter[range]': '1min',
        'aggregate': 'minute',
        'filter[url]': '*',
    }
    
    fetchCurrentUsers.url = fetchCurrentUsers.url || bindParamsToUrl(new global.URL(baseUrl), fetchCurrentUsers.params)
    
    return new Promise((resolve, reject) => {
        fetch(fetchCurrentUsers.url, {
            credentials: 'include'
        }).then((response) => {
            return response.json()
        }).then((data) => {
            resolve(data.data[0].attributes.activeusers)
        }).catch((error) => {
            reject(error)
        })
    })
}

/**
 * Возвращает Promise со средним временем чтения
 * @memberof api/linkpulse
 * */
export function fetchAvgViewtime() {
    fetchAvgViewtime.params = fetchAvgViewtime.params || {
        'query': 'query',
        'filter[range]': '1min',
        'aggregate': 'total',
        'sort': 'viewtime',
        'field[viewtime]': 'median',
    }
    
    fetchAvgViewtime.url = fetchAvgViewtime.url || bindParamsToUrl(new global.URL(baseUrl), fetchAvgViewtime.params)
    
    return new Promise((resolve, reject) => {
        fetch(fetchAvgViewtime.url, {
            credentials: 'include'
        }).then((response) => {
            return response.json()
        }).then((data) => {
            const time = moment(new Date(0, 0, 0, 0, 0, data.data[0].attributes.viewtime, 0)).format('mm:ss')
            resolve(time)
        }).catch((error) => {
            reject(error)
        })
    })
}

