/**
 * Возвращает Promise с количеством просмотров, заголовками, URL-ами и относительными процентами просмотров TOP статей
 * @memberof api/linkpulse
 * */
export function fetchTopArticles(maxArticlesCount: number, sort: 'pageviews' | 'viewtime' = 'pageviews') {
    const params = {
        'query': 'query',
        'aggregate': 'url',
        'include': 'url',
        'filter[range]': '1min',
        'filter[pagetype]': 'article',
        'filter[pageviews]': '>1',
        'field[url]': 'one',
        'field[pageviews]': 'sum',
        'field[viewtime]': 'median',
        'sort': '-' + sort,
        'page[limit]': maxArticlesCount,
    }

    const url = bindParamsToUrl(new global.URL(baseUrl), params)

    return new Promise((resolve, reject) => {
        fetch(url, {
            credentials: 'include'
        }).then((response) => {
            return response.json()
        }).then(async (data) => {

            let pageviewsRaw = data.data,
            titlesUrlsRaw = data.included

            const summ = pageviewsRaw.reduce(function(summ, current) {
                return summ + current.attributes.pageviews
            }, 0)

            /**
             * @memberof api/linkpulse
             * @param topArticles Данные TOP статей
             * @param pageviews Количество просмотров статьи
             * @param title Заголовок статьи
             * @param url URL статьи
             * @param percent % просмотров статьи относительно самой просматриваемой статьи в этой выборке
             * */
            type TopArticles = [{
                viewtime: string,
                pageviews: number,
                title: string,
                url: string,
                percent: number
            }]

            let topArticles: TopArticles = pageviewsRaw
            .map(function (element, index) {
                let value = [getPropSafe(() => pageviewsRaw[index].attributes.pageviews) || 0, moment(new Date(0, 0, 0, 0, 0, getPropSafe(() => pageviewsRaw[index].attributes.viewtime) || 0, 0)).format('mm:ss')],
                url = getPropSafe(() => pageviewsRaw[index].attributes.url) || '',
                title = getPropSafe(() => titlesUrlsRaw[index].attributes.title) || '',
                caption = getPropSafe(() => titlesUrlsRaw[index].attributes.author) || '',
                percent = value[0] * 100 / summ

                return {value, title, caption, url, percent}
            })
    
            topArticles = await Promise.all(topArticles.map(async (item, index) => {
                if (index >= 10) {
                    return item
                }
    
                item.counters = await fetchTopSocialReferers(item.url)
    
                return item
            }))

            resolve(topArticles)
        }).catch((error) => {
            reject(error)
        })
    })
}

/**
 * Создает объект с датами для запроса
 * @memberof api/linkpulse
 * */
const getTimlineDate = (range: string = 'today') => {
    const date = {
        from: new Date(),
        to: new Date(),
    }
    
    date.from.setUTCHours(0)
    date.from.setUTCMinutes(0)
    date.from.setUTCSeconds(0)
    date.from.setUTCMilliseconds(0)
    
    date.to.setUTCHours(23)
    date.to.setUTCMinutes(59)
    date.to.setUTCSeconds(0)
    date.to.setUTCMilliseconds(0)
    
    if (range === 'weekAgo') {
        date.from.setUTCDate(date.from.getUTCDate() - 7)
        date.to.setUTCDate(date.to.getUTCDate() - 7)
    }
    
    return date
}

/**
 * Возвращает Promise с количеством пользователей в течение дня и неделю назад
 * @memberof api/linkpulse
 * */
export function fetchUsersTimeline(range: string = 'today', url?: string): any {
    fetchUsersTimeline.consts = {
        YEAR_POS: 0,
        MONTH_POS: 3,
        DAY_POS: 5,
        TIME_POS: 7,
        HOURS_POS: 9,
    }
    
    const {from, to} = getTimlineDate(range)
    
    const params = {
        'query': 'query',
        'filter[from]': from.toISOString(),
        'filter[to]': to.toISOString(),
        'field[activeusers]': 'one',
        'aggregate': 'minute',
        'sort': 'id',
        'page[limit]': 24 * 60,
        'filter[url]': '',
    }
    
    if (url) {
        params['filter[url]'] = url
    }
    
    const fetchUrl = bindParamsToUrl(new global.URL(baseUrl), params)
    
    return new Promise((resolve, reject) => {
        fetch(fetchUrl, {
            credentials: 'include'
        }).then((response) => {
            return response.json()
        }).then((data) => {
            const rawData = data.data.filter((item, i) => i % 20 === 0)
            const timeline = []
            
            rawData.forEach((item, i) => {
                /*
                * Парсим строку с датой и с форматом вида: 201710090013 (YYYYMMDDHHmm)
                * */
                const parsedDate = String(item.id).split('').map((char, i) => {
                    if (i === fetchUsersTimeline.consts.MONTH_POS) return char + '/'
                    if (i === fetchUsersTimeline.consts.DAY_POS) return char + '/'
                    if (i === fetchUsersTimeline.consts.TIME_POS) return char + ' '
                    if (i === fetchUsersTimeline.consts.HOURS_POS) return char + ':'
                    return char
                }).join('')
                
                const date = new Date(parsedDate)
                date.setMinutes(global.Math.round(date.getMinutes() / 20) * 20)
    
                /*
                * получаем ключ в виде интервала от 0 до 71, т.е. каждые 20 минут
                * */
                timeline[i] = {
                    date: date.toISOString(),
                    total: item.attributes.activeusers,
                }
            })
            
            resolve(timeline)
        }).catch((error) => {
            reject(error)
        })
    })
}

/**
 * Возвращает Promise с массивом данных о топе источников
 * @memberof api/linkpulse
 * */
export function fetchTopReferers(maxItems: number = 10, url?: string): any {
    const params = {
        'query': 'query',
        'filter[range]': '1min',
        'filter[site]': '!www.wday.ru',
        'field[url]': 'one',
        'field[toSite]': 'one',
        'field[clicks]': 'sum',
        'aggregate': 'domain',
        'sort': '-clicks',
        'include': 'url',
        'page[limit]': maxItems,
        'filter[toUrl]': '',
    }
    
    if (url) {
        params['filter[toUrl]'] = url
    }
    
    const fetchUrl = bindParamsToUrl(new global.URL(baseUrl), params)
    
    return new Promise((resolve, reject) => {
        fetch(fetchUrl, {
            credentials: 'include'
        }).then((response) => {
            return response.json()
        }).then((data) => {
            const referersRawList = data.data
            
            if (!referersRawList) {
                resolve([])
            }
            
            const summ = referersRawList.reduce(function(summ, current) {
                return summ + current.attributes.clicks
            }, 0)

            let referersList
    
            referersList = referersRawList
            .map((item) => {
                let value = item.attributes.clicks,
                title = item.id || item.attributes.url || 'Неизвестный источник',
                url = '',
                percent = value * 100 / summ
                
                return {value, title, url, percent}
            })
            
            resolve(referersList)
        }).catch((error) => {
            reject(error)
        })
    })
}

/**
 * Возвращает Promise с массивом данных о топе социальных сетей
 * @memberof api/linkpulse
 * */
export function fetchTopSocialReferers(url?: string): any {
    const params = {
        'query': 'query',
        'filter[range]': 'today',
        'filter[site]': '*vk.com*|*ok.ru*|*instagram.com*|*facebook.com*|*news-clck.yandex*|*zen.yandex*',
        'field[url]': 'list',
        'field[toUrl]': 'one',
        'field[clicks]': 'sum',
        'aggregate': 'domain',
        'sort': '-clicks',
        'filter[toUrl]': '',
    }
    
    const socialIds = ['vk', 'ok', 'facebook', 'instagram', 'yandex']
    const socialTitles = ['Вконтакте', 'Одноклассники', 'Facebook', 'Instagram', 'Zen и Я.Новости']
    
    if (url) {
        params['filter[toUrl]'] = url
    }
    
    const fetchUrl = bindParamsToUrl(new global.URL(baseUrl), params)
    
    return new Promise((resolve, reject) => {
        fetch(fetchUrl, {
            credentials: 'include'
        }).then((response) => {
            return response.json()
        }).then((data) => {
            let referersRawList = data.data
            let referersList = socialIds.map(id => {
                return {
                    id: id,
                    title: socialTitles[socialIds.indexOf(id)],
                    value: 0,
                }
            })
            
            if (!referersRawList) {
                resolve(referersList)
            }
    
            referersList = referersList.map((item) => {
                referersRawList.forEach(({id, attributes}) => {
                    if (id.includes(item.id)) {
                        item.value += attributes.clicks
                    }
                })
                
                return item
            })
            
            resolve(referersList)
        }).catch((error) => {
            reject(error)
        })
    })
}

/**
 * Возвращает Promise с массивом данных о топе следующих переходов для ссылки
 * @memberof api/linkpulse
 * */
export function fetchUrlTopNextPages(maxItems: number = 10, url: string): any {
    const params = {
        'query': 'query',
        'filter[range]': '1min',
        'filter[url]': url,
        'filter[toUrl]': `!${url}`,
        'filter[site]': 'www.wday.ru',
        'filter[toSite]': 'www.wday.ru',
        'field[url]': 'one',
        'field[toUrl]': 'one',
        'field[clicks]': 'sum',
        'aggregate': 'toUrl',
        'sort': '-clicks',
        'include': 'toUrl',
        'page[limit]': maxItems,
    }
    const fetchUrl = bindParamsToUrl(new global.URL(baseUrl), params)
    
    return new Promise((resolve, reject) => {
        fetch(fetchUrl, {
            credentials: 'include'
        }).then((response) => {
            return response.json()
        }).then((data) => {
            const referersRawList = data.data
            const referersUrlsList = data.included
            
            if (!referersRawList) {
                resolve([])
            }
            
            const summ = referersRawList.reduce(function(summ, current) {
                return summ + current.attributes.clicks
            }, 0)
            
            let referersList
            
            referersList = referersRawList
            .map(function (item, index) {
                let value = getPropSafe(() => item.attributes.clicks) || 0,
                title = getPropSafe(() => referersUrlsList[index].attributes.title) || getPropSafe(() => item.attributes.id),
                caption = getPropSafe(() => referersUrlsList[index].attributes.author),
                url = getPropSafe(() => referersUrlsList[index].attributes.url) || getPropSafe(() => item.attributes.id),
                percent = value * 100 / summ
                
                return {value, title, caption, url, percent}
            })
            
            resolve(referersList)
        }).catch((error) => {
            reject(error)
        })
    })
}

/**
 * Возвращает Promise с данными о странице
 * @memberof api/linkpulse
 * */
export function fetchPageInfo(url: string): any {
    const params = {
        'query': 'query',
        'filter[range]': '1min',
        'aggregate': 'minute',
        'include': 'url',
        'filter[url]': url,
        'field[url]': 'one',
        'field[pageviews]': 'sum',
        'field[clicks]': 'sum',
        'field[viewtime]': 'median',
    }
    
    const fetchUrl = bindParamsToUrl(new global.URL(baseUrl), params)
    
    return new Promise((resolve, reject) => {
        fetch(fetchUrl, {
            credentials: 'include'
        }).then((response) => {
            return response.json()
        }).then(async (data) => {
            const dataMeta = getPropSafe(() => data.data[0].attributes)
            const dataIncluded = getPropSafe(() => data.included[0].attributes)
            const time = moment(new Date(0, 0, 0, 0, 0, getPropSafe(() => dataMeta.viewtime), 0)).format('mm:ss')
            const counters = await fetchTopSocialReferers(url)
            const page = {
                count: getPropSafe(() => dataMeta.pageviews),
                viewtime: time,
                recirculation: getRecirculation(getPropSafe(() => dataMeta.pageviews), getPropSafe(() => dataMeta.clicks)),
                title: getPropSafe(() => dataIncluded.title),
                description: getPropSafe(() => dataIncluded.description),
                url: getPropSafe(() => dataIncluded.srcUrl),
                image: getPropSafe(() => dataIncluded.image),
                author: getPropSafe(() => dataIncluded.author),
                date: getPropSafe(() => dataIncluded.published),
                counters: counters,
            }
            
            resolve(page)
        }).catch((error) => {
            reject(error)
        })
    })
}

/**
 * Возвращает Promise с данными о странице
 * @memberof api/linkpulse
 * */
export function fetchDevices(url?: string): any {
    const params = {
        'query': 'query',
        'filter[range]': '1min',
        'aggregate': 'channel',
        'field[activeusers]': 'one',
        'filter[url]': '',
    }
    
    if (url) {
        params['filter[url]'] = url
    }
    
    const fetchUrl = bindParamsToUrl(new global.URL(baseUrl), params)
    
    return new Promise((resolve, reject) => {
        fetch(fetchUrl, {
            credentials: 'include'
        }).then((response) => {
            return response.json()
        }).then((data) => {
            const summ = data.data.reduce(function(summ, current) {
                return summ + current.attributes.activeusers
            }, 0)
            const devices = data.data.map((item) => {
                const title = item.id
                const value = item.attributes.activeusers
                const percent = item.attributes.activeusers * 100 / summ
                const url =  ''
                
                return {title, value, percent, url}
            })
            .sort((a, b) => a.value < b.value)
            
            resolve(devices)
        }).catch((error) => {
            reject(error)
        })
    })
}



