// @flow

/**
 * Tops.js - контейнер для страницы дэшборда
 * @module containers/Tops
 * @requires module:config
 * @requires module:components/Nav
 * @requires module:components/Counter
 * @requires module:components/List
 * */

import React, {Component} from 'react'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import ReactTooltip from 'react-tooltip'

import {dataProviderTypes} from '../../config'
import {Layout} from '../../components/Layout/Layout'
import {List} from '../../components/List/List'

import {clearAllIntervals} from '../../common'

import * as topArticlesActions from '../../actions/TopArticlesActions'

import './Tops.css'

/**
 * @memberof containers/Tops
 * */
type Props = {
    topArticlesActions(): {
        fetchTopArticles(): any,
    },
    topArticles: {
        topArticles: [{
            value: number,
            title: string,
            url: string,
            percent: number
        }]
    },
}

const topArticlesSortIcons: string[] = ['visibility', 'watch_later']
const topArticlesSortKeys: string[] = ['pageviews', 'viewtime']

/**
 * @namespace Tops
 * */
export class Tops extends Component<Props, void> {
    /**
     * */
    static props:Props
    
    /**
     * */
    static ARTICLES_LIMIT: number = 100
    
    /**
     * */
    static PROVIDER_TYPE: string = dataProviderTypes.YANDEX
    
    /**
     * */
    static TOP_ARTICLES_SORT: string = 'pageviews'
    
    /**
     * */
    constructor() {
        super()
        this.topArticlesSort = this.topArticlesSort.bind(this)
        this.topArticlesRange = this.topArticlesRange.bind(this)
    }
    
    /**
     * Получаем данные после отрисовки
     * */
    componentDidMount() {
        const {fetchTopArticles} = this.props.topArticlesActions
        fetchTopArticles({
            maxArticlesCount: this.constructor.ARTICLES_LIMIT,
            sort: this.constructor.TOP_ARTICLES_SORT,
            providerType: this.constructor.PROVIDER_TYPE,
        })
    }
    
    /**
     * Отключаем компонент
     * */
    componentWillUnmount() {
        clearAllIntervals()
    }
    
    /**
     * Отрисовка компонента при изменении состояния
     * */
    render() {
        const {topArticles} = this.props.topArticles
        
        return (
        <Layout>
            <div className="Tops">
                <div className="row">
                    <div className="col-9 Tops__content">
                        <List title="Топ статей за период" data={topArticles} icons={topArticlesSortIcons} hasFilter={true} hasDateRange={true} onSortClick={this.topArticlesSort} onDateRangeChange={this.topArticlesRange} />
                    </div>
                    <div className="col-3 p-0 Tops__secondSidebar">
                    </div>
                </div>
        
                <ReactTooltip place="right" type="dark" effect="solid"/>
            </div>
        </Layout>
        )
    }
    
    /**
     * Сортируем список статей
     * */
    topArticlesSort(type: string) {
        this.constructor.TOP_ARTICLES_SORT = topArticlesSortKeys[topArticlesSortIcons.indexOf(type)]

        this.props.topArticlesActions.fetchTopArticles({
            maxArticlesCount: this.constructor.ARTICLES_LIMIT,
            sort: this.constructor.TOP_ARTICLES_SORT,
            providerType: this.constructor.PROVIDER_TYPE,
        })
    }
    
    /**
     * Сортируем список статей
     * */
    topArticlesRange(startDate: string, endDate: string) {
        this.props.topArticlesActions.fetchTopArticles({
            maxArticlesCount: this.constructor.ARTICLES_LIMIT,
            sort: this.constructor.TOP_ARTICLES_SORT,
            providerType: this.constructor.PROVIDER_TYPE,
            dateRange: [startDate, endDate]
        })
    }
}

function mapStateToProps(state) {
    return {
        topArticles: state.topArticles,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        topArticlesActions: bindActionCreators(topArticlesActions, dispatch),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Tops)



